import {required, requiredIf} from "vuelidate/lib/validators";

export default {
  layout: 'AdminTemplate',
  name: "users"
  ,
  data() {
    return {
      setRoleUserId: 0,
      roleSelected: [],
      roleOptions: [
        { text: 'Administrator', value: '1' },
        { text: 'Tutor', value: '2' },
        { text: 'Student', value: '3' },
      ],
      overlay_show: false ,
      visible: true,
      active_options:[
        { value: 2, text: 'All' },
        { value: 0, text: 'Pending' },
        { value: -1, text: 'Disable' },
        { value: 1, text: 'Enable' },
      ],
      searchData: {
        pageIndex: 1,
        rowsCount: 0,
        perPage: 10,
        email: "",
        name: "",
        lastname: "",
        active: 2 // 2=all , 0= disable , 1= enable
      },
      t_fields:[
        {
          key: 'email',
          label: 'Email/User',
          sortable: true
        },
        {
          key: 'name',
          label: 'Name',
          sortable: true
        },
        {
          key: 'lastname',
          label: 'Last Name',
          sortable: true
        },
        {
          key: 'roles',
          label: 'Role',
          sortable: false
        },
        {
          key: 'acceptStatus',
          label: 'Status',
          sortable: false
        },
        {
          key: 'createdAt',
          label: this.$t('CreatedAt'),
          sortable: false
        },
        {
          key: 'econfirmed',
          label: 'Email Confirmed',
          sortable: false
        },
        {
          key: 'view',
          label: 'Action',
          sortable: false
        },
        {
          key: 'setrole',
          label: this.$t('SetRole'),
          sortable: false
        }
      ],
      t_items: []
    }
  },
  mounted()
  {
    this.search()
  },
  methods:
  {
    setRoleDialog(item){
      this.roleSelected = item.rolesId
      this.setRoleUserId = item.id
      this.showModal()
    },
    setRoles(){
      try {
        const formData = {
          roles : this.roleSelected ,
          userId: this.setRoleUserId
        }
        this.$axios.post("/users/setRole" , formData).then((res)=>{
          if(res.data.result === true)
          {
            this.hideModal()
            this.search(this.searchData.pageIndex)
          }
        }).catch((err) => {
          this.makeToast("danger" , "Error" , "search_error" )
        })
      }catch (e) {

      }

    },
    setRoleCancel(){
      this.hideModal()
    },
    showModal() {
      this.$refs['role-modal'].show()
    },
    hideModal() {
      this.$refs['role-modal'].hide()
    },

    hasRole: function (role, roles) {
      if (roles !== undefined)
        return roles.indexOf(role) !== -1;
      return false
    },

    makeToast(variant = 'success', header = 'Message', msg = '')
    {
      this.$bvToast.toast(this.$t(msg), {
          title: header,
          variant: variant,
          solid: true
        })
     },

    search(index)
    {
      if(index > 0)
        this.searchData.pageIndex = index
      else
        this.searchData.pageIndex = 1
      this.overlay_show = true;
      this.$axios.post('users/list', this.searchData).then((res) => {
        this.t_items = res.data.rows
        this.searchData.rowsCount = res.data.row_count;
        this.overlay_show = false;

      }).catch(error => {
        this.overlay_show = false;
        this.makeToast("danger" , "Error" , "search_error" )
        console.log(error)
      });

    }

  }

}
